body {
  font-family: BlinkMacSystemFont,-apple-system,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans","Helvetica Neue",Helvetica,Arial,sans-serif;
  color: #4a4a4a;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
}

#main-container  h1 {
  font-size: 1.8rem;
  font-weight: 600;
  padding-top: 20px;
}

#root {
  width: 90%;
  min-width: 200px;
  margin: 0 auto;
  font-family: 'Noto Sans', 'Helvetica', sans-serif;
  text-align: center;
}
@media screen and (max-width: 700px) {
  #root {
    width: 100%;
  }
}
#root a {
  text-decoration: none;
}
#root .btn:focus {
  outline: none;
}
#root .btn.btn-create {
  margin-top: -20px;
  min-width: 160px;
}

.driverApp {
  width: 80%;
  max-width: 800px;
  margin: 30px auto 40px;
  text-align: center;
  max-width: 768px;
  padding: 0;
}
@media screen and (max-width: 700px) {
  .driverApp {
    width: 90%;
  }
}
.driverApp .driver {
  width: 90%;
  text-align: left;
  background: #fafafa;
  border-radius: 10px;
  margin: 10px auto;
}
.driverApp .driver a {
  text-decoration: none;
}
.driverApp .driver a a:hover,
.driverApp .driver a a:focus,
.driverApp .driver a a:visited {
  text-decoration: none;
}
.driverApp .driver .ingredients {
  width: 90%;
  margin: 20px auto;
  border-collapse: collapse;
}
.driverApp .driver .ingredients td {
  width: 100%;
  border: 1px solid #d2d2d2;
  padding: 10px;
}
.driverApp .driver .driverHeader {
  background: #6a8ed6;
  position: relative;
  color: #fafafa;
  font-size: 1.4rem;
  letter-spacing: 1px;
  margin-top: 0;
  margin-bottom: 0;
  border-radius: 5px;
  padding: 10px 10px 10px 25px;
  transition: all .25s linear;
  text-align: left;
}
.driverApp .driver .driverHeaderLink {
  background: transparent;
  border: 0;
  cursor: pointer;
  outline: 0;
  width: 100%;
}
.driverApp .driver .driverHeader.active {
  border-radius: 5px 5px 0 0;
  background: #3b71de;
}
.driverApp .driver .driverHeader .glyphicon-plus-sign,
.driverApp .driver .driverHeader .glyphicon-minus-sign {
  position: absolute;
  right: 10px;
  top: 10px;
}
@media screen and (max-width: 350px) {
  .driverApp .driver .driverHeader {
    font-size: 1.8rem;
  }
}
.driverApp .driver .driverInfo {
  width: 90%;
  margin: 0 auto;
  overflow: hidden;
  display: none;
  padding-top: 20px;
}
.driverApp .driver .driverInfo p b {
  display: inline-block;
  min-width: 200px;
}
.driverApp .driver .sub-header {
  font-size: 1.8rem;
  margin: 15px auto;
  width: 80%;
  text-align: center;
}
.driverApp .driver .directions {
  width: 90%;
  margin: 0 auto;
  padding-bottom: 10px;
}
.driverApp .driver .edit-btn {
  background: #3387de;
  color: #fafafa;
  margin-top: 20px;
  margin-bottom: 15px;
  position: relative;
}
.driverApp .driver .edit-btn:hover {
  background: #3282b4;
}

.modal {
  text-align: left;
  position: absolute;
  top: 0;
  z-index: 10000;
}
.modal .text-danger {
  margin-top: 10px;
  margin-bottom: 0;
}
@media screen and (max-width: 350px) {
  .modal .modal-footer {
    text-align: center;
  }
}
.modal button.btn.btn-danger {
  position: absolute;
  left: 10px;
  bottom: 15px;
}
@media screen and (max-width: 350px) {
  .modal button.btn.btn-danger, .modal button.btn.btn-default, .modal button.btn.btn-primary {
    width: 80%;
    position: relative;
    margin: 5px auto;
    top: 0;
    left: 0;
  }
}
.modal label {
  margin-top: 10px;
}

.loginBox {
  padding: 30px;
  border: #ccc 1px solid;
  border-radius: 4px;
  box-shadow: #ccc 0px 0px 5px 0px;
  max-width: 350px;
  margin: 0 auto;
}

#container label {
  text-align: left;
  display: block;
  font-weight: 600;
  margin-bottom: 0.3rem;
}

#container .form-control {
  margin-bottom: 10px;
}

.noList {
  font-size: 1.2rem;
  color: #7e929c;
}

  